import React from 'react'
import Project from '../Project/Project'
import { projects } from '../Project/data.project'

import { Wrapper } from './projects.components'
import { WorksInfo } from './projects.components'

const Projects = () => {
  return (
    <>
      <WorksInfo id='works'>Experience:</WorksInfo>
      {projects.map((project) => {
        return (
          <Wrapper id={`works${project.id}`} key={project.id}>
            <Project project={project} />
          </Wrapper>
        )
      })}
    </>
  )
}

export default Projects
