import styled from 'styled-components'

export const Link = styled.h3`
  font-size: 1.5rem;
  position: absolute;
  margin: 0;
  padding: 0;
  width: 45%;
  text-align: left;
  margin-left: 20px;
  word-wrap: break-word;
  text-align: justify;
  top: 170px;
  @media screen and (max-width: 500px) {
    font-size: 1rem;
  }
`
export const Header = styled.a`
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  font-size: 1.5rem;
  @media screen and (max-width: 500px) {
    font-size: 1rem;
  }
`

export const Desc = styled.span`
  position: absolute;
  top: 300px;
  left: 20px;
  font-size: 1.3rem;
  white-space: pre-line;
  @media screen and (max-width: 500px) {
    font-size: 0.8rem;
  }
`
